.wrapper {
    position: relative;
    width: 420px;
    height: 450px;
    background: transparent;
    border: 2px solid rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(30px);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    color: #fff;
    display: flex;
    align-items: center;
    overflow: hidden;
    /* لمنع تجاوز المحتوى */
}

.wrapper .form-box {
    width: 100%;
    padding: 40px;
    position: absolute;
    /* لتصير النماذج فوق بعضها البعض */
    transition: transform 0.18s ease;
    /* انتقال ناعم لتغيير المواقع */
}

.wrapper .form-box.login {
    transform: translateX(0);
    /* وضع النموذج الافتراضي */
}

.wrapper .form-box.register {
    transform: translateX(400px);
    /* إبعاد نموذج التسجيل عن الشاشة */
}

.wrapper.active .form-box.login {
    transform: translateX(-400px);
    /* إبعاد نموذج تسجيل الدخول عن الشاشة */
}

.wrapper.active .form-box.register {
    transform: translateX(0);
    /* عرض نموذج التسجيل */
}

.wrapper.active {
    height: 520px;
}

form h1 {
    font-size: 36px;
    text-align: center;
}

form .input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
}

.input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid rgba(255, 255, 255, .1);
    border-radius: 40px;
    font-size: 16px;
    color: #fff;
    padding: 20px 45px 20px 20px;
}

.input-box input::placeholder {
    color: #fff;
}

.input-box .icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
}

form .remember-forgot {
    display: flex;
    justify-content: space-between;
    font-size: 14.5px;
    margin: -15px 0 15px;
}

form .remember-forgot label input {
    accent-color: #fff;
    margin-right: 4px;
}

.remember-forgot a {
    color: #fff;
    text-decoration: none;
}

.remember-forgot a:hover {
    text-decoration: underline;
}

form button {
    width: 100%;
    height: 45px;
    background: #fff;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    font-size: 16px;
    color: #333;
    font-weight: 700;
}

form .register-link {
    font-size: 14.5px;
    text-align: center;
    margin: 20px 0 15px;
}

.register-link p a {
    color: #fff;
    text-decoration: none;
    font-weight: 600;
}

.register-link p a:hover {
    text-decoration: underline;
}